.App {
    text-align: center;
    padding: 20px;
}

h2 {
    position: absolute;
    top: 10%;
    left: 25%;
}

button {
    border-radius: 5px;
    padding: 5px;
    font-size: 14px;
    position: absolute;
}

.sort-button {
    top: 10%;
    left: 6.5%;
}

.options-form {
    position: absolute;
    height: 85px;
    display: flex;
    align-items: column;
    /* justify-content: space-around; */
    top: 100px;
    left: 45%;
}

.options-form input {
    top: 190px;
}

.options-form button {
    width: 160px;
}

button:hover:enabled {
    background-color: #d9d9d9;
}

.active-link {
    color: lightskyblue;
}

a {
    color: #808080;
}
